@import './variables.sass';

.wrapper {
    width: 100%;
    height: auto;
    min-height: 100vh;
    max-height: 100vh;
    overflow: hidden;
    color: #333;
    background-color: $bgMain;
    font-size: 14px;
    display: flex;
    flex-direction: column;
    font-family: 'Muli', sans-serif; }

.container {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-left: auto;
    margin-right: auto;
    box-sizing: border-box; }

.container-fluid {
    width: 100%;
    height: 100%;
    display: flex;
    overflow: auto; }
.text-limit {
    max-width: 100%;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden; }

.input-control {
    width: 100%;
    box-shadow: none;
    background: none;
    padding: 9px 12px 10px;
    border: 1px solid;
    cursor: text;
    box-sizing: border-box;
    background: white;
    border-radius: 4px;
    border-color: white;
    font-size: 14px; }

