.table {
    width: 100%;
    margin-bottom: 1rem;
    background-color: transparent;
    th {
        color: #52505B;
        background-color: #F3F2F4;
        border-color: #f9f9f9;
        vertical-align: bottom;
        border: 1px solid #f9f9f9;
        &:first-child {
            position: fixed;
            left: 5px; } }
    th {
        padding: 6px 10px;
        box-sizing: border-box; }
    td {
        padding: 8px 10px;
        box-sizing: border-box; }
    tbody tr:nth-of-type(even) {
        background-color: #F8F8F9; } }

table {
    border-collapse: collapse; }

